/*

 */
import Vue from 'vue'
import VueRouter from 'vue-router'


import Home from '../pages/Home.vue'
import Account from '../pages/home_all/Account.vue' // 
import Notifications from '../pages/Notifications/Notifications.vue' // 
import add_account_management from '../pages/home_all/add_account_management.vue' // 
import devices from '../pages/devices/devices.vue' // 
import devices_edit from '../pages/devices/devices_edit.vue' // 
import devices_types from '../pages/devices/devices_types.vue' // 
import Device_Groups from '../pages/Device_Groups/Device_Groups.vue' // 
import Device_Groups_add from '../pages/Device_Groups/Device_Groups_add.vue' // 
import lockers from '../pages/lockers/lockers.vue' // 
import lockers_all from '../pages/lockers/lockers_all.vue' // 
import Users_1 from '../pages/users/Users_1.vue' // 
import Users_all from '../pages/users/Users_all.vue' // 
import BayManagement_1 from '../pages/BayManagement/BayManagement_1.vue' // 
import Bay_Recent_Activities from '../pages/BayManagement/Bay_Recent_Activities.vue' // 
import login_index from '../pages/login/login_index.vue' // 


Vue.use(VueRouter)

export default new VueRouter({
  routes: [{
      path: '/home',
      component: Home,
      children: [
        
      ]
    },
   
    // redirectAccount
    {
      path: '/Notifications',
      component: Notifications // 
    },
    {
      path: '/Account',
      component: Account // 
    },
    {
      path: '/add_account_management',
      component: add_account_management // 
    },
     {
       path: '/devices',
       component: devices,
       meta: {
         showExtraMenus: true
       } // 
     },
    {
      path: '/devices_edit',
      component: devices_edit // 
    },
    {
      path: '/devices_types',
      component: devices_types // 
    },
    {
      path: '/Device_Groups',
      component: Device_Groups // 
    },
    {
      path: '/Device_Groups_add',
      component: Device_Groups_add // 
    },
    {
      path: '/lockers',
      component: lockers // 
    },
    {
      path: '/lockers_all',
      component: lockers_all // 
    },
    {
      path: '/Users_1',
      component: Users_1 // 
    },
    {
      path: '/Users_all',
      component: Users_all // 
    },
    {
      path: '/BayManagement_1',
      component: BayManagement_1 // 
    },
    {
      path: '/Bay_Recent_Activities',
      component: Bay_Recent_Activities // 
    },
    {
      path: '/login_index',
      component: login_index // 
    }
  ]
})


