<template>
  <!--  -->
  <div class="login-container">
    <!--  -->
    <img src="../img/1_2.png" alt="HonestWaves Icon" class="login-icon" />
    <!-- HonestWaves  -->
    
    <!--  -->
    <div class="form-container">
         <!-- Admin Portal  -->
    <h2 class="sub-title">Admin Portal</h2>
      <form class="login-form" @submit.prevent="handleLogin">
        <!--  -->
        <input
          type="text"
          class="form-input"
          placeholder="Username"
          v-model="username"
          required
        />
        <!--  -->
        <input
          type="password"
          class="form-input"
          placeholder="Password"
          v-model="password"
          required
        />
        <!--  -->
        <button type="submit" class="login-btn">Login</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      username: "",
      password: ""
    };
  },
  methods: {
    handleLogin() {
      // 
      console.log("", this.username, this.password);
      this.$router.push("/home");
    }
  }
};
</script>

<style scoped>
/*  */
.login-container {
  width: 100vw;
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  background-color: #fff; /*  */
  gap: 15px; /*  */
}

/*  */
.login-icon {
  width: 260px; /* 60px33% */
  height: auto;
  object-fit: contain;
}

/* + */
.main-title {
  font-size: 28px;
  font-weight: 700;
  color: #000;
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: "Segoe UI", sans-serif;
}

/*  */
.sub-title {
  font-size: 18px;
  color: #000;
    font-weight: 700;
    margin-bottom: 30px;
  padding: 0;
  text-align: center;
  font-family: "Segoe UI", sans-serif;
}

/* + */
.login-hint {
  font-size: 14px;
  color: #666;
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: "Segoe UI", sans-serif;
}

/*  */
.form-container {
  width: 800px;
  padding: 25px;
  border: 2px solid #252B4266;
  border-radius: 12px; /*  */
  background-color: #fff;
}

/*  */
.login-form {
  display: flex;
  flex-direction: column;
  gap: 12px; /*  */
}

/*  */
.form-input {
    text-align:center;
  width: 100%;
  padding: 12px 15px;
  border: 1px solid #ddd;
  border-radius: 8px; /*  */
  background-color: #F0F0F0;
  font-size: 14px;
  color: #333;
  font-family: "Segoe UI", sans-serif;
  box-sizing: border-box; /*  */
}

/*  */
.form-input:focus {
  outline: none;
  /* border-color: #F0F0F0; */
  border: 1px solid #002F6C; /*  */
}

/*  */
.login-btn {
    width: 70%;
    margin: 0 auto;
    font-weight: 700;
  padding: 8px 28px;
  border: 1px solid #ddd; /*  */
  border-radius: 8px; /*  */
  background-color: #002F6C; /*  */
  color: #fff;
  font-size: 18px;
  cursor: pointer;
  font-family: "Segoe UI", sans-serif;
  transition: none; /* hover */
  margin-top: 8px;
}
</style>


